___CSS_LOADER_EXPORT___.push([module.id, `.carousel {
  display: flex;
  width: clamp(600px, 90vw, 1200px);
  height: clamp(400px, 60vw, 800px);
  border: 1px solid #2c2c2c;
  overflow: hidden;
  position: relative;
}

.x-previous-button,
.x-next-button {
  width: 40px;
  height: 40px;
  position: absolute;
  top: clamp(200px, 30vw, 400px);
  background-color: rgba(255, 255, 255, 0.4);
  opacity: 0.7;
  border: none;
  border-radius: 6px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 45%;
  cursor: pointer;
}

.x-previous-button:hover,
.x-next-button:hover {
  background-color: rgba(255, 255, 255, 0.6);
  opacity: 0.8;
}

.x-previous-button {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  left: 10px;
}

.x-next-button {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  right: 10px;
}

.x-imgLink-div {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: clamp(200px, 35vw, 500px);
  bottom: 30px;
  height: 20px;
  width: 200px;
  gap: 10px;
}

.x-imgLink-button {
  width: 14px;
  height: 14px;
  border-radius: 7px;
  border: none;
  cursor: pointer;
  background-color: #9c9c9cb7;
  display: none;
  text-align: center;
}

.x-current-imgLink-button {
  background-color: rgb(236, 236, 236);
  width: 16px;
  height: 16px;
  border-radius: 8px;
}

.image-list {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.x-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: none;
}

.x-visible {
  display: block;
}
